              <path
                d="M263.5776 384.6656c-16.9472 0-30.72-13.7728-30.72-30.72V240.5376H111.9744c-16.9472 0-30.72-13.7728-30.72-30.72s13.7728-30.72 30.72-30.72h151.6032c16.9472 0 30.72 13.7728 30.72 30.72v144.128c0 16.9472-13.7216 30.72-30.72 30.72zM912.0256 847.872h-152.9856c-16.9472 0-30.72-13.7728-30.72-30.72v-149.6576c0-16.9472 13.7728-30.72 30.72-30.72s30.72 13.7728 30.72 30.72v118.9376h122.2656c16.9472 0 30.72 13.7728 30.72 30.72s-13.7728 30.72-30.72 30.72z"
