        <p>{{item.artists[0].name}}</p>
      </div>
    </section>
    <section class="card-list" ref="list" v-if="select=='3'">
      <div
        class="slide"
        v-for="item in artists"
        :key="item.id"
        @click="play(item.external_urls.spotify)"
      >
        <div class="image">
          <img :src="item.images[0]&&item.images[0].url" alt="" width="100%" height="100%" />
          <svg t="1619573965038" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2535" width="64" height="64"><path d="M512 1024A512 512 0 1 1 512 0a512 512 0 0 1 0 1024z m3.008-92.992a416 416 0 1 0 0-832 416 416 0 0 0 0 832zM383.232 287.616l384 224.896-384 223.104v-448z" fill="#d81e06" p-id="2536"></path></svg>
        </div>
        <h2>{{ item.name }}</h2>
        <p>Followers: {{item.followers.total}}</p>
      </div>
    </section>
    <section class="spt" v-if="spt" ref="plugin">
      <iframe
        :src="url"
        width="100%"
        height="380"
        frameborder="0"
        allowtransparency="true"
        allow="encrypted-media"
      ></iframe>
    </section>
  </div>
</template>

<script>
import { spotify } from "@/network/api.js";
import mh from '@/components/mental/musicheader'

let token;

export default {
  async created() {
    token = await spotify.getToken();
    this.genre = await spotify.getGenres(token);
    if(localStorage.getItem('search')) {
      let obj = JSON.parse(localStorage.getItem('search'))
      if(obj.type == '1') {
        this.guess = await spotify.getGr(obj.term, token);
        this.select = '1'
      }else if(obj.type == '2') {
        this.tracks = await spotify.getTrack(obj.term, token)
        this.select = '2'
      }else {
        this.artists = await spotify.getArt(obj.term, token)
        this.select = '3'
      }
    }else {
      this.guess = await spotify.getGr(this.genre[1].id, token);
    }
  },
  data() {
    return {
      genre: "",
      guess: [],
      get: "More Music",
      spt: false,
      url: "",
      subject: 'Tracks You Might Like',
      select: '1',
      artists: [],
      tracks: [],
    };
  },
  methods: {
    getall() {
      if (this.get == "More Music") {
        this.$refs.list.style.height = "100%";
        this.get = "Less Music";
         $('html,body').animate({scrollTop: 1000}, "slow");
      } else {
        this.$refs.list.style.height = "750px";
        this.get = "More Music";
      }
    },
    play(uri) {
      this.spt = true;
      let i 
      switch(this.select) {
        case '1':
          i = uri.indexOf("playlist");
          break;
        case '2':
          i = uri.indexOf("track");
          break;
        case '3':
          i = uri.indexOf('artist');
          break;
      }
      this.url = uri.substring(0, i) + "embed/" + uri.substring(i);
      // console.log(this.$refs.plugin)
      this.$nextTick(() => {
        $('html,body').animate({scrollTop:this.$refs.plugin.offsetTop - 100}, "slow");
      })
      // $('html,body').animate({scrollTop: , "slow");
    },
    async search(val) {
      localStorage.setItem('search',JSON.stringify({
        type: val[0],
        term: val[1],
      }))
      if(val[0] == 1) {
        this.guess = await spotify.getGr(val[1], token);
        this.subject = val[2]
        this.select = '1'
      }else if(val[0] == 2) {
        this.tracks = await spotify.getTrack(val[1], token)
        this.subject = val[1]
        this.select = '2'
      }else {
        this.artists = await spotify.getArt(val[1], token)
        this.subject = val[1]
        this.select = '3'
        // this.subject = val[1]
      }
    }
  },
  mounted() {},
  components: {
    'mh-com':mh
  }
};
</script>
<style scoped>
.intro {
  position: relative;
  /* left: 50%; */
  width: 100%;
  max-width: 1300px;
  min-width: 1000px;
  /* transform: translate(-50%); */
  font-family: Arial, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
}
.search {
  width: 100%;
  height: 300px;
}
.card-list {
  margin: 0 auto;
  width: 90%;
  height: 750px;
  display: flex;
  overflow: hidden;
  justify-content: center;
  flex-wrap: wrap;
}
.card-list .slide {
  width: 210px;
  height: 300px;
  padding: 15px;
  margin: 20px;
  background: #000;
  cursor: pointer;
  border-radius: 16px;
}
.card-list .slide:hover {
  background: rgb(121, 120, 120);
}

.card-list .slide:hover img {
  transform: scale(1.1);
  opacity: 0.5;
}
.card-list .slide:hover svg {
  opacity: 1;
}
.card-list img {
  transition: .3s linear;
}
.card-list .image {
  margin: 0 auto;
  width: 190px;
  height: 190px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}
.card-list .image svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}
.card-list h2:hover{
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  text-shadow: none;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}
.card-list h2 {
  margin-top: 10px;
  color: white;
}
.card-list p {
  margin-top: 10px;
}
.title {
  width: 90%;
  height: 40px;
  /* background-color: bisque; */
  display: flex;
  margin: auto;
  margin-top: 20px;
}
.title .side {
  width: 5px;
  height: 25px;
  background-color: #47b8e0;
  margin: 7px 10px 5px 10px;
  border-radius: 10px;
}
.title .rightside {
  width: 95%;
}
.title .subject {
  float: left;
  font-size: 22px;
  margin: 7px 0 0 10px;
  /* font-family: "Trebuchet MS", Arial, Helvetica, sans-serif; */
  font-family: Arial, Helvetica, sans-serif;
}
.title .all {
  float: right;
  font-size: 18px;
  margin: 7px 0 0 10px;
  /* font-family: "Trebuchet MS", Arial, Helvetica, sans-serif; */
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.title .all:hover {
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  text-shadow: none;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}
.slide p {
  color: white;
  opacity: 0.8;
  font-size: 12px;
  text-overflow: ellipsis;
}
.spt {
  width: 90%;
  margin: 20px auto;
  background: #444444;
}

</style>