<template>
  <div class="mh-intro">
    <div class="subtitle">
      <p>Use our website to get a recommendation of Music based on your specific interests and preferences!</p>
    </div>
    <div class="mh-search">
      <el-input
        v-if="select != 1"
        placeholder="Search..."
        v-model="musicterm"
        class="input-with-select"
        @keydown.native.enter="searchbtn"
      >
        <el-select v-model="select" slot="prepend" placeholder="Select">
          <el-option label="Genre" value="1"></el-option>
          <el-option label="Track" value="2"></el-option>
          <el-option label="Artist" value="3"></el-option>
        </el-select>
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchbtn"
        ></el-button>
      </el-input>
      <el-autocomplete
        v-else
        class="inline-input"
        v-model="musicterm"
        :fetch-suggestions="querySearch"
        placeholder="Search..."
        @keydown.native.enter="searchbtn"
      >
        <el-select v-model="select" slot="prepend" placeholder="Select">
          <el-option label="Genre" value="1"></el-option>
          <el-option label="Track" value="2"></el-option>
          <el-option label="Artist" value="3"></el-option>
        </el-select>
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchbtn"
        ></el-button>
      </el-autocomplete>
    </div>
  </div>
</template>

<script>
export default {
  props: ["category"],
  data() {
    return {
      select: "1",
      musicterm: "",
      cate: []
    };
  },
  created() {},
  methods: {
    searchbtn() {
      if(this.select == 1) {
        let r = ''
        this.cate.forEach(item => {
          if(item.value == this.musicterm) {
            r = item.id
          }
        })
        this.$emit('search',[this.select, r, this.musicterm])
      }else {
        this.$emit('search',[this.select, this.musicterm])
      }
    },
    querySearch(queryString, cb) {
      let genre = this.cate;
      let results = queryString ? genre.filter(this.createFilter(queryString)) : genre;
      cb(results);
    },
    // filter the genre when user typing
    createFilter(queryString) {
      return (cate) => {
        return (cate.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
  },
  watch: {
    category(newVal) {
      if (newVal instanceof Array) {
        newVal.forEach((element) => {
          this.cate.push({
            value: element.name,
            id: element.id,
          });
        });
      }
    },
  },
};
</script>
<style>
.mh-intro {
  width: 100%;
  height: 300px;
  background-image: url("../../img/music.jpg");
  background-position-y: 400px;
  background-size: cover;
  box-shadow: #47b8e0 0 5px 5px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.mh-search {
  width: 700px;
  height: 60px;
}
.mh-search .el-select .el-input {
  width: 100px;
}
.mh-intro .subtitle{

  padding: 20px;
  border-radius: 15px;
  /* border: 1px solid #6c25a9; */
  position: absolute;
  left: 30px;
  bottom: 10px;
  width: 500px;
  color: #fff;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  background: rgba(0, 0, 0, 0.5);
}
</style>